<template>
    <c-theme-provider>
        <c-reset />
        <Header />
        <Sidebar title="Cholli.to" app_version="0.9.9" @toView="handleView" />
        <keep-alive> <router-view> </router-view> </keep-alive>
    </c-theme-provider>
</template>

<script>
import { CThemeProvider, CReset } from "@chakra-ui/vue";
import Header from "./components/Header";
import Sidebar from "./components/Sidebar";

export default {
    name: "App",
    components: {
        Sidebar,
        CThemeProvider,
        CReset,
        Header,
    },

    data() {
        return {
            view: null,
            speakers: ["telegram", "twitter", "facebook"],
        };
    },

    sockets: {
        connect() {
            this.showSuccess("Conectado con el servidor!");
        },
    },

    methods: {
        handleView(view) {
            this.view = view;
        },

        handlePublishItem(item) {
            let can_publish = false;
            item.bot_id = this.$store.state.currentSelectedBotId;
            if (item.send_to && item.send_to.length >= 1) {
                can_publish = true;
            }
            if (can_publish) {
                this.showInfoSuccess("Enviando chollo..");
                return item;
            }
            this.showError(
                "No se ha establecido un Altavoz para publicar el chollo."
            );
        },

        showError(message) {
            this.$toast({
                title: "Error.",
                description: message,
                status: "error",
                duration: 10000,
                variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
            });
        },
        showInfoSuccess(message) {
            this.$toast({
                title: "Info.",
                description: message,
                status: "info",
                duration: 9000,
                variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
            });
        },

        showSuccess(message) {
            this.$toast({
                title: "Conexin Exitosa.",
                description: message,
                status: "success",
                duration: 10000,
                variant: "subtle",
            });
        },
        
        saveItem(item) {
            // store the item in the database, hotfix
            this.$socket.client.emit("add_item", item);
            this.$socket.client.emit("get_chollos_bot_data");
        },
    },

    beforeMount() {
        this.$root.$on("save_item", (item) => this.saveItem(item));
        this.$socket.$subscribe("set_bots_simple_data", (data) => {
            this.$store.state.bots = data;
        });
        this.$socket.$subscribe("disconnect_this_shit", ()=>{
            window.open('/logout', '_self');
        });
        this.$socket.client.emit("get_bots_simple_data")
        this.$socket.client.emit("get_inputs_values");
        this.$root.$on("publish_item", (item) => {
            if (this.handlePublishItem(item)) {
                this.$socket.client.emit("publish_item", item);
            }
        });
    },
};
</script>

