<template>
  <c-stack spacing="5" w="100%">
    <c-form-control>
      <c-form-label>Titulo</c-form-label>
      <c-input
        v-model="title"
        v-on:input="$root.$emit('updateTextTitle', title)"
        placeholder="Titulo del Chollo"
      />
    </c-form-control>

    <c-form-control mt="4">
      <c-form-label>Precio real</c-form-label>
      <c-input
        v-model="regular_price"
        placeholder="Precio real"
        v-on:input="$root.$emit('updateTextRegularPrice', regular_price)"
      />
    </c-form-control>

    <c-form-control mt="4">
      <c-form-label>Precio Oferta</c-form-label>
      <c-input
        v-model="price"
        placeholder="Precio Oferta"
        v-on:input="$root.$emit('updateTextPrice', price)"
      />
    </c-form-control>
  </c-stack>
</template>

<script>
export default {
  props: {
    chollo_data: Object,
  },
  data() {
    return {
      title: "",
      regular_price: "",
      price: "",
      coupon: "",
      market: ""
    };
  },

  mounted() {
    this.title = this.chollo_data.title;
    this.price = this.chollo_data.price ? this.chollo_data.price + "" : "";
    this.coupon = this.chollo_data.discount ? this.chollo_data.discount : "";
    this.market = this.chollo_data.market;
    this.regular_price = this.chollo_data.regular_price ? this.chollo_data.regular_price + "" : ""

    this.$root.$on("destroyP5Instance", () => {
      this.$destroy();
    });
    this.$root.$once("getImageFormData", () => {
      this.$root.$emit("updateTextTitle", this.title);
      this.$root.$emit("addMarket", this.market);
      this.$root.$emit("updateTextPrice", this.price);
      this.$root.$emit("updateTextRegularPrice", this.regular_price);
    });
  },
};
</script>

