<template>
    <c-box class="config__box-container">
        <div class="config__background"></div>
        <c-flex
            class="config__flex-container"
            flexDirection="column"
            justifyContent="space-between"
        >
            <c-box display="flex" justifyContent="space-between">
                <c-heading id="header">{{ name }} </c-heading>
                <c-tooltip label="Activo" placement="bottom">
                    <c-box h="15px">
                        <c-icon name="circle" color="green.400" size="5" />
                    </c-box>
                </c-tooltip>
            </c-box>
            <c-box
                class="config__button-container"
                textAlign="end"
                marginTop="35px"
            >
                <c-button
                    variant-color="blue"
                    @click="openDrawer()"
                    :isLoading="drawer.is_loading"
                    loadingText="Cargando.."
                >
                    Abrir
                </c-button>
            </c-box>
            <ConfigurationDrawer
                :name="name"
                :status="drawer.status"
                @saveSignal="handleSaveSignal"
            >
                <div v-if="!isolatedConfig">
                    <ConfigurationInput
                        v-for="data in params"
                        v-bind:key="data.id"
                        :placeholder="data.placeholder"
                        :name="data.name"
                        :show="data.show"
                        :var_name="data.var_name"
                        @saveInputs="handleInputSignal"
                    />
                </div>
                <div v-if="isolatedConfig">
                    <ConfigurationSelect
                        :name="params.name"
                        @saveInputs="handleInputSignal"
                    />
                </div>
            </ConfigurationDrawer>
        </c-flex>
    </c-box>
</template>

<script>
import ConfigurationDrawer from "./ConfigurationDrawer";
import ConfigurationInput from "./ConfigurationInput";
import ConfigurationSelect from "./ConfigurationSelect";

export default {
    components: {
        ConfigurationDrawer,
        ConfigurationInput,
        ConfigurationSelect,
    },
    props: {
        name: String,
        returnEventName: String,
        params: [Array, Object],
        isolatedConfig: {
            type: Boolean,
            default: false,
            required: false,
        },
    },
    sockets: {
        connect() {
            console.log("CONFIGURATION BOX SOCKET CONNECTED");
        },
    },
    data() {
        return {
            inputs: {},
            test_input: "",
            drawer: { status: { isOpen: false }, is_loading: true },
        };
    },

    methods: {
        openDrawer() {
            this.drawer.status.isOpen = !this.drawer.status.isOpen;
        },
        handleSaveSignal() {
            this.inputs.bot_id = this.$route.params.id;
            this.$socket.client.emit(this.returnEventName, this.inputs);
        },
        handleInputSignal(name, value) {
            this.inputs[name] = value;
            this.$root.$emit("onSavedInputs", name, value);
        },
    },
    mounted() {
        this.$socket.$subscribe("on_set_bot_data", () => {
            this.drawer.is_loading = false;
        });

        this.$root.$on("onMountedParams", (name, value) => {
            this.inputs[name] = value;
        });
    },
};
</script>

