<template>
  <c-popover trigger="click" v-slot="{ onClose }" placement="bottom">
    <c-popover-trigger>
      <c-box>
        <c-tooltip label="Historial / Logs" placement="bottom">
          <c-box id="notification-button" class="header__bell-button">
            <c-icon name="history" id="bell-icon"></c-icon>
          </c-box>
        </c-tooltip>
      </c-box>
    </c-popover-trigger>
    <c-popover-content
      z-index="4"
      color="white"
      background-color="blue.700"
      border-color="blue.700"
      height="250px"
      widht="100%"
    >
      <c-popover-header pt="4" font-weight="bold" border="0">
        Historial / Logs
      </c-popover-header>
      <c-popover-arrow />
      <c-popover-close-button />
      <c-popover-body
        background-color="blue.620"
        overflowY="scroll"
        height="60%"
      >
        <LogsItem
          v-for="log in logs"
          v-bind:key="log.id"
          :name="log.title"
          :url="log.url"
        />

        <c-flex v-if="logs.length == 0" w="100%" h="100%" justifyContent="center" padding="16px">
          <c-spinner
            thickness="4px"
            speed="0.65s"
            empty-color="white"
            color="blue.200"
            size="4rem"
            label="Cargando.."
          />
        </c-flex>
      </c-popover-body>

      <c-popover-footer
        border="0"
        d="flex"
        align-items="center"
        justify-content="space-between"
        pb="4"
      >
        <c-box font-size="sm"></c-box>

        <c-button-group size="sm">
          <c-button variant-color="red" id="next" @click="onClose">
            Cerrar
          </c-button>
        </c-button-group>
      </c-popover-footer>
    </c-popover-content>
  </c-popover>
</template>


<script>
import LogsItem from "./LogsItem";

export default {
  components: {
    LogsItem,
  },
  data() {
    return {
      logs: [],
    };
  },
  methods: {
    generateId() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, (c) => {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    },
  },
  mounted() {
    this.$socket.$subscribe("add_to_logs", (data) => {
      data.id = this.generateId();
      this.logs.push(data);
    });
  },
};
</script>

