class ImageEventHandler {

    constructor(data) {

        this.text = {
            title: data.title,
            price: data.price,
            regularPrice: data.regularPrice,
            market: data.market || 'elcorteingles'
        }
    }

    onUpdateTitle(text) {
        this.text.title = text;
    }

    onUpdatePrice(text) {
        this.text.price = text;
    }

    onUpdateRegularPrice(text) {
        this.text.regularPrice = text;
    }
    onUpdateMarket(text) {
        this.text.market = text;
    }
    // onUpdateDiscountCode(price){}
}

class ImageFactory extends ImageEventHandler {

    constructor(sketch, data, imageData) {
        super(data);
        this.__sketch = sketch;
        this.imageData = imageData;

    }

    createProductImage() {

        this.productTemplateImage = this.__sketch.createImg(
            this.imageData.product_template_path,
            "",
            "no-cors"
        );
        this.productTemplateImage.hide();

    }

    createBackground() {
        this.backgroundTemplateImage = this.__sketch.createImg(
            this.imageData.main_template_path
        );
        this.backgroundTemplateImage.hide();
    }
}


class ImageBuilder extends ImageFactory {

    constructor(sketch, data) {
        super(sketch, data, data.imageData)
        this.fonts = data.fonts;
        this.sketch = sketch;


    }

    updateRegularPrice() {
        this.sketch.textFont(this.fonts.robotoLigth);
        this.sketch.textSize(65);
        this.sketch.fill("black");
        this.sketch.text(this.text.regularPrice, 85, this.sketch.width / 1.05, this.sketch.width);
        this.sketch.textAlign(this.sketch.LEFT);
    }

    updatePrice() {
        this.sketch.textFont(this.fonts.robotoBlack);
        this.sketch.textSize(70);
        this.sketch.fill("#003da1");
        this.sketch.text(this.text.price, 0, this.sketch.height / 1.06, this.sketch.width);
        this.sketch.textAlign(this.sketch.CENTER);
    }

    updateTitle() {
        this.sketch.textFont(this.fonts.robotoLigth);
        this.sketch.fill("#FFFFF");

        let text = this.sketch.split(this.text.title, ",,");
        let position_y = 200;
        let position_x = 320;
        let text_size = 70;

        this.sketch.textAlign(this.sketch.RIGHT);

        text.forEach((value, index) => {
            if (index == 1) {
                this.sketch.textFont(this.fonts.robotoBlack);
            } else {
                this.sketch.textSize(text_size);
            }
            this.sketch.text(
                value,
                position_x,
                position_y,
                this.sketch.width / 1.5,
                this.sketch.height / 2
            );
            this.sketch.textAlign(this.sketch.RIGHT);
            position_y += 75;
            text_size -= 20;
        });
    }
    updateMarket() {
        this.sketch.textFont(this.fonts.robotoLigth);
        this.sketch.textSize(25);
        this.sketch.fill("#fffff");
        this.sketch.text(this.text.market, 795, 714, 400, 400);
        this.sketch.textAlign(this.sketch.CENTER)
    }
    renderProductImage() {
        this.sketch.background("#fffff");
        this.createProductImage();

        try {
            this.sketch.image(
                this.productTemplateImage,
                this.sketch.height / 50,
                this.sketch.width / 3,

                this.sketch.width / 2,
                this.sketch.width / 2
            );
        } catch {
            this.createProductImage();
        }
    }

    renderBackgroundImage() {
        try {
            this.sketch.image(
                this.backgroundTemplateImage,
                0,
                0,
                this.sketch.width,
                this.sketch.height
            );

        } catch {

            this.createBackground();
        }

    }

    render() {

        this.renderProductImage();
        this.renderBackgroundImage();
        this.updateTitle();
        this.updatePrice();
        this.updateRegularPrice();
        this.updateMarket();
    }
}


export default ImageBuilder