<template>
    <div class="main__content--title-container">
        <header class="header__title--text">{{ title }}</header>
    </div>
</template>

<script>
import { CBox, CFlex, CText } from "@chakra-ui/vue";

export default {
    CBox,
    CText,
    CFlex,

    data() {
        return { title: "title" };
    },

    beforeCreate() {
        this.$root.$on("add_title", (title) => {
            this.title = title;
        });
    },
};
</script>

