<template>
  <div>
    <c-alert-dialog
      :is-open="isOpen"
      :least-destructive-ref="$refs.cancelRef"
      :on-close="closeDialog"
    >
      <c-alert-dialog-overlay />
      <c-alert-dialog-content>
        <c-alert-dialog-header font-size="lg" font-weight="bold">
          {{ responseMessage }}
        </c-alert-dialog-header>
        <c-alert-dialog-body>
          <c-input readonly :value="link" id="url-text"></c-input>
        </c-alert-dialog-body>
        <c-alert-dialog-footer>
          <c-button
            variantColor="blue"
            @click="
              () => {
                copyToClipboard();
                closeDialog();
                showInfoSuccess('El enlace ha sido copiado exitosamente!');
              }
            "
            ml="3"
          >
            Copiar
          </c-button>
        </c-alert-dialog-footer>
      </c-alert-dialog-content>
    </c-alert-dialog>
    <c-button
      variant-color="green"
      size="sm"
      @click="execShortenerCommand"
      :isLoading="isLoading"
    >
      Acortar enlace
    </c-button>
  </div>
</template>
<script>
export default {
  props: {
    url: String,
    onCleanFunc: Function,
  },
  data() {
    return {
      isOpen: false,
      isLoading: false,
      link: "",
      responseMessage: "",
    };
  },
  methods: {
    closeDialog() {
      this.isOpen = false;
    },
    openDialog() {
      this.isOpen = true;
    },
    execShortenerCommand() {
      this.isLoading = true;
      if (this.isValidUrl(this.url)) {
        this.$socket.client.emit("create_shortener_link", { url: this.url });
        this.onCleanFunc();
      } else {
        this.showError(
          "Error el enlace es invlido, verifica e intntalo otra vez."
        );
        this.isLoading = false;
      }
    },
    copyToClipboard() {
      /* Get the text field */
      let copyText = document.querySelector("#url-text");
      copyText.setAttribute("type", "text"); //  hidden 
      copyText.select();
      /* Select the text field */
      copyText.setSelectionRange(0, 99999); /* For mobile devices */

      /* Copy the text inside the text field */
      document.execCommand("copy");
      copyText.setAttribute("type", "hidden");
      window.getSelection().removeAllRanges();
    },
    isValidUrl(str) {
      let pattern = new RegExp(
        ""
          .concat(
            "^(",
            "[-a-zA-Z0-9^\\p{L}\\p{C}\\u00a1-\\uffff@:%_\\+.~#?&//=]{2,256}",
            "){1}",
            "(\\.[a-z]{2,4}){1}",
            "(\\:[0-9]*)?",
            "(/[-a-zA-Z0-9\\u00a1-\\uffff\\(\\)@:%,_\\+.~#?&//=]*)?",
            "([-a-zA-Z0-9\\(\\)@:%,_\\+.~#?&//=]*)?",
            "$"
          )
      );
      return pattern.test(str);
    },
    showInfoSuccess(message) {
      this.$toast({
        title: "Enlace copiado.",
        description: message,
        status: "success",
        duration: 9000,
        variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
      });
    },
    showError(message) {
      this.$toast({
        title: "Error.",
        description: message,
        status: "error",
        duration: 9000,
        variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
      });
    },
  },

  mounted() {
    this.$socket.$subscribe("set_shortener_link", (response) => {
      this.link = response.data.url;
      this.responseMessage = response.info;
      this.openDialog();
      this.isLoading = false;
    });
  },
};
</script>