<template>
  <c-box>
    <c-alert-dialog
      :is-open="isOpen"
      :least-destructive-ref="$refs.cancelRef"
      :on-close="closeDialog"
    >
      <c-alert-dialog-overlay />
      <c-alert-dialog-content>
        <c-alert-dialog-header font-size="lg" font-weight="bold">
          {{ title }}
        </c-alert-dialog-header>
        <c-alert-dialog-body>
          {{ message }}
        </c-alert-dialog-body>
        <c-alert-dialog-footer>
          <c-button ref="cancelRef" @click="closeDialog">
            Cancelar
          </c-button>
          <c-button
            :variantColor="type.action.color"
            @click="
              () => {
                action();
                closeDialog();
              }
            "
            ml="3"
          >
            Confirmar
          </c-button>
        </c-alert-dialog-footer>
      </c-alert-dialog-content>
    </c-alert-dialog>
    <c-button
      :variantColor="type.color"
      @click="openDialog"
      fontSize="14px"
      marginRight="4px"
    >
      {{ name }}
    </c-button>
  </c-box>
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      required: true,
    },
    type: {
      type: Object,
      required: true,
    },
    name: {
      type: String,
      required: true,
    },
    action: {
      type: Function,
      required: true,
    },
    message: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      isOpen: false,
    };
  },
  methods: {
    closeDialog() {
      this.isOpen = false;
    },
    openDialog() {
      this.isOpen = true;
    },
  },
};
</script>