<template>
    <c-flex align="center" justify="start" class="main__container--chollos">
        <c-box class="main__tabs-container">
            <c-flex class="chollos__configuration-container">
                <ConfigurationBox
                    name="Telegram"
                    returnEventName="set_telegram_tokens"
                    :params="[
                        {
                            var_name: 'token',
                            name: 'Token del bot',
                            placeholder: 'Token..',
                            show: false,
                            id: generateId(),
                            value: '',
                        },
                        {
                            var_name: 'chat_id',
                            name: 'Nombre del Canal',
                            placeholder: '@Chollos20',
                            show: false,
                            id: generateId(),
                            value: '',
                        },
                    ]"
                />

                <ConfigurationBox
                    name="Twitter"
                    returnEventName="set_twitter_tokens"
                    :params="[
                        {
                            var_name: 'access_token',
                            name: 'Access Token',
                            placeholder: 'Access token de twitter',
                            show: false,
                            id: generateId(),
                            value: '',
                        },
                        {
                            var_name: 'secret_token',
                            name: 'Secret Token',
                            placeholder: 'Secret token de twitter',
                            show: true,
                            id: generateId(),
                            value: '',
                        },
                        {
                            var_name: 'consumer_key',
                            name: 'Consumer Key',
                            placeholder: 'Consumer key de twitter',
                            show: false,
                            id: generateId(),
                            value: '',
                        },
                        {
                            var_name: 'consumer_secret',
                            name: 'Consumer Secret',
                            placeholder: 'Consumer Secret de twitter',
                            show: true,
                            id: generateId(),
                            value: '',
                        },
                    ]"
                />
                <ConfigurationBox
                    name="Facebook"
                    returnEventName="set_facebook_account"
                    :params="[
                        {
                            var_name: 'fb_access_token',
                            name: 'Access Token',
                            placeholder: 'Token de acceso de Facebook',
                            show: false,
                            id: generateId(),
                            value: '',
                        },
                    ]"
                />
                <ConfigurationBox
                    name="Acortadores"
                    returnEventName="set_shortener"
                    :isolatedConfig="true"
                    :params="{ name: 'Seleciona un Acortador' }"
                />
            </c-flex>
        </c-box>
    </c-flex>
</template>

<script>
import ConfigurationBox from "../components/configuration/ConfigurationBox";
export default {
    components: {
        ConfigurationBox,
    },
    data() {
        return {
            inputs: {},
        };
    },
    methods: {
        generateId() {
            return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
                /[xy]/g,
                (c) => {
                    var r = (Math.random() * 16) | 0,
                        v = c == "x" ? r : (r & 0x3) | 0x8;
                    return v.toString(16);
                }
            );
        },
    },
    activated() {
        this.$socket.client.emit("bot_alive", {
            bot_id: this.$route.params.id,
        });
    },
    mounted() {
        this.$socket.$subscribe("on_set_bot_data", (payload) => {
            this.inputs = payload;
            try {
                this.$root.$emit("set_inputs_values", payload);
            } catch {
                console.log("err");
            }
        });

        this.$root.$on("getConfigInputsValues", (func) => {
            func(this.inputs);
        });

        this.$root.$on("onSavedInputs", (name, value) => {
            this.inputs[name] = value;
        });
        try {
            this.$socket.$subscribe("set_bot_status", (status) => {
                if (status) {
                    this.$root.$emit(
                        "add_title",
                        "Bots >> Configuracin  >> " +
                            this.$store.state.currentBotName
                    );
                    this.$socket.client.emit("get_bot_data", {
                        bot_id: this.$route.params.id,
                    });
                } else {
                    this.$router.push("/bots");
                }
            });
            return;
        } catch {
            this.$toast({
                title: "Error.",
                description:
                    "Ha ocurrido un error al intentar obtener el status del bot.",
                status: "error",
                duration: 9000,
                variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
            });
            this.$router.push("/bots");
        }
        this.$route.push("/bots");
    },
};
</script>

<style scoped>
.chollos__configuration-container {
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
}

@media only screen and (max-width: 1200px) {
    .chollos__configuration-container {
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }
}
</style>