<template>
  <div>
    <c-box>
      <c-form-label for="title" fontSize="0.75rem"
        >Personalizar Imagen</c-form-label
      >
      <c-switch
        color="blue"
        id="title"
        :value="switchValue"
        :isChecked="switchValue"
        v-model="switchValue"
        @click="open"
      />
    </c-box>

    <c-modal
      :initial-focus-ref="() => $refs.initialRef"
      :is-open="isOpen"
      :on-close="close"
      size="full"
    >
      <c-modal-content
        class="image-modal__content"
        ref="content"
        h="auto"
        w="calc(100% - 250px)"
      >
        <c-modal-header>Personalizar Imagen</c-modal-header>

        <c-modal-close-button />
        <c-modal-body class="image-editor__body">
          <c-box>
            <c-stack
              class="image-stack__container"
              flexDirection="row"
              justifyContent="Space-around"
            >
              <c-box
                class="image-editor__view"
                ml="8px"
                textAlign="-webkit-center"
              >
                <ImageView v-if="isOpen" :image_data="image_data" />
              </c-box>
              <c-box h="100%" class="image-editor__form" p="14px">
                <ImageForm v-if="isOpen" :chollo_data="{ ...chollo_data }"
              /></c-box>
            </c-stack>
          </c-box>
        </c-modal-body>
        <c-modal-footer>
          <c-button mr="3" @click="close">Cancelar</c-button>
          <c-button
            variant-color="blue"
            @click="
              $root.$emit('saveImage', onSaveImage);

              close();
            "
            >Guardar</c-button
          >
        </c-modal-footer>
      </c-modal-content>
      <c-modal-overlay />
    </c-modal>
  </div>
</template>

<script>
import ImageView from "./components/ImageView";
import ImageForm from "./components/ImageForm";
import Template from "./templates/template.png";
export default {
  components: {
    ImageView,
    ImageForm,
  },

  props: {
    chollo_data: Object,
    eventHandler: Function,
  },
  data() {
    return {
      isOpen: false,
      switchValue: false,
      image_data: {
        main_template_path: Template,
        product_template_path: "",
      },
      image_view_id: "",
    };
  },
  methods: {
    generateViewId() {
      this.image_view_id = this.generateId();
      return this.image_view_id;
    },
    open() {
      if (this.chollo_data.custom_image && !this.switchValue) {
        this.eventHandler(this.chollo_data.custom_image);
      }
      if (!this.switchValue) {
        this.isOpen = true;
        return;
      }
      if (this.switchValue) {
        this.eventHandler(this.chollo_data.original_url);
      }
    },
    close() {
      this.isOpen = false;
      this.$root.$emit("destroyP5Instance");
      this.showed = false;
    },

    onSaveImage(callback) {
      callback();
    },
    generateId() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, (c) => {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    },
  },

  mounted() {
    if (this.chollo_data.custom_image) {
      this.switchValue = true;
    }
    this.$socket.$subscribe("add_image", (data) => {
      this.image_data.product_template_path = data.url;
    });

    this.$socket.client.emit("get_image_url", {
      url: this.chollo_data.original_url,
    });

    this.$root.$on("updateBanner", (url) => {
      this.eventHandler(url);
    });
  },

  beforeDestroy() {
    this.$root.$off("updateBanner");
    this.$el.parentNode.removeChild(this.$el);
  },
};
</script>

