<template>
    <c-flex align="center" justify="start" class="main__container--chollos">
        <c-box class="main__tabs-container">
            <c-tabs variant="enclosed-colored" :default-index="0" size="md">
                <c-tab-list>
                    <c-tab
                        v-for="bot in bots"
                        v-bind:key="bot.bot_id"
                        v-bind:value="bots.name"
                        >{{ bot.name }}</c-tab
                    >
                </c-tab-list>

                <c-tab-panels class="tabs__panel-container" v-if="render">
                    <c-tab-panel
                        outline="none"
                        v-for="bot_data in items"
                        v-bind:key="bot_data.id"
                    >
                        <!-- Panel card content chollitos -->
                        <ChollosItem
                            v-for="chollos in bot_data.chollos"
                            v-bind:key="chollos.bot_id"
                            :chollo_data="chollos"
                            :is_saved="true"
                            :showed="() => 1"
                        />
                    </c-tab-panel>
                </c-tab-panels>
            </c-tabs>
        </c-box>
    </c-flex>
</template>

<script>
import { CBox, CFlex, CText, CButton, CIcon, CTabs } from "@chakra-ui/vue";
import ChollosItem from "../components/ChollosItem";

export default {
    CBox,
    CText,
    CFlex,
    CButton,
    CIcon,
    CTabs,

    components: {
        ChollosItem,
    },

    data: function () {
        return {
            items: {},
            bots: {},
            render: true
        };
    },

    methods: {

        deleteItem(item) {
            this.render = false;
            this.bots.forEach((_, index) => {
                let bot_id = this.items[index].bot_id;
                if (bot_id == item.bot_id) {
                    this.items[index].chollos.forEach((chollo, chollo_index) => {
                        if (chollo.id == item.id) {
                            this.items[index].chollos.splice(chollo_index, 1);
                            this.$nextTick(()=>{
                                this.render = true;
                            });
                         // console.log(this.items[index].chollos);
                        }
                    });
                }
            });
            this.$socket.client.emit("delete_item", item);
        },

        editItem(item) {
            this.$socket.client.emit("edit_item", item);
            this.$root.$emit("requestItemChange", item);
        },
    },
    activated() {
        this.$root.$emit("add_title", "Chollos programados");
        this.$socket.client.emit("get_chollos_bot_data");
    },

    beforeMount() {
        // this.$socket.$subscribe("update_saved_items", this.saveItemsHandler);
        this.$socket.client.emit("get_chollos_bot_data");
        this.$socket.$subscribe("set_chollos_bot_data", (data) => {
            this.bots = data.bots;
            this.bots.forEach((element, index) => {
                this.items[index] = {
                    bot_id: element.bot_id,
                    chollos: data.chollos_data[element.bot_id],
                };
            });
        });

        this.$root.$on("edit_item", (item) => this.editItem(item));
        this.$root.$on("delete_item", (item) => this.deleteItem(item));
    },
};
</script>

