<template>
    <c-flex justify="start" class="main__container--chollos">
        <c-box class="main__tabs-container">
            <c-flex class="selector__wrapper" mb="3" w="100%" p="4px" flexDirection="row">
                <c-menu :close-on-select="false">
                    <c-menu-button
                        right-icon="chevron-down"
                        variant-color="blue"
                    >
                        <c-icon name="filter" size="14px" /> Ordenar por
                    </c-menu-button>
                    <c-menu-list min-width="240px">
                        <c-menu-option-group
                            default-value="new"
                            title="Chollos"
                            type="radio"
                            textAlign="initial"
                            @change="
                                (sort) => {
                                    currentOrder = sort;
                                }
                            "
                        >
                            <c-menu-item-option value="new"
                                >Nuevos</c-menu-item-option
                            >
                            <c-menu-item-option value="popular"
                                >Populares</c-menu-item-option
                            >
                            <c-menu-item-option value="featured"
                                >Destacados</c-menu-item-option
                            >
                        </c-menu-option-group>
                        <c-menu-divider />
                    </c-menu-list>
                </c-menu>
                <c-box
                    as="button"
                    rounded="md"
                    bg="#ff0055"
                    color="white"
                    px="4"
                    p="8px"
                    ml="4px"
                    @click="
                        () => {
                            createChollosDrawer = !createChollosDrawer;
                        }
                    "
                    ><c-icon name="plus" fontSize="16px"></c-icon>Crear
                    Chollo</c-box
                >
                <c-box class="refresh__button">
                    <c-icon-button
                        :isLoading="refreshLoading"
                        aria-label="Refrescar"
                        icon="sync"
                        @click="refreshCommand"
                    ></c-icon-button>
                </c-box>
            </c-flex>

            <c-tabs
                variant-color="blue"
                @change="handleItems"
                :default-index="0"
                size="md"
            >
                <c-tab-list>
                    <c-tab fontSize="0.9rem">SoyDeChollos</c-tab>
                    <c-tab fontSize="0.9rem">Chollometro</c-tab>
                    <c-tab fontSize="0.9rem">Michollo</c-tab>
                    <c-tab fontSize="0.9rem">Enchollados</c-tab>
                    <c-tab fontSize="0.9rem">Enchollados Pack Ahorro</c-tab>
                    <c-tab fontSize="0.9rem">ChollosDelSuper</c-tab>
                </c-tab-list>

                <c-tab-panels class="tabs__panel-container" size="sm">
                    <c-tab-panel outline="none">
                        <!-- Panel card content soydechollos -->
                        <ChollosItem
                            v-for="chollo in this.items[this.currentOrder][
                                'soy_de_chollos'
                            ]"
                            v-bind:key="chollo.id"
                            :chollo_data="chollo"
                            :showed="() => actualIndex == 0"
                        />
                    </c-tab-panel>
                    <c-tab-panel>
                        <!-- Panel card content chollometro -->
                        <ChollosItem
                            v-for="chollo in this.items[this.currentOrder][
                                'chollometro'
                            ]"
                            v-bind:key="chollo.id"
                            :chollo_data="chollo"
                            :showed="() => actualIndex == 1"
                        />
                    </c-tab-panel>
                    <c-tab-panel>
                        <!-- Panel card content michollo -->
                        <ChollosItem
                            v-for="chollo in this.items[this.currentOrder][
                                'michollo'
                            ]"
                            v-bind:key="chollo.id"
                            :chollo_data="chollo"
                            :showed="() => actualIndex == 2"
                        />
                    </c-tab-panel>
                    <c-tab-panel>
                        <!-- Panel card content enchollados -->
                        <ChollosItem
                            v-for="chollo in this.items[this.currentOrder][
                                'enchollados'
                            ]"
                            v-bind:key="chollo.id"
                            :chollo_data="chollo"
                            :showed="() => actualIndex == 3"
                        />
                    </c-tab-panel>
                    <c-tab-panel>
                        <!-- Panel card content enchollados -->
                        <ChollosItem
                            v-for="chollo in this.items[this.currentOrder][
                                'enchollados_pack_ahorro'
                            ]"
                            v-bind:key="chollo.id"
                            :chollo_data="chollo"
                            :showed="() => actualIndex == 4"
                        />
                    </c-tab-panel>
                    <c-tab-panel>
                        <!-- Panel card content enchollados -->
                        <ChollosItem
                            v-for="chollo in this.items[this.currentOrder][
                                'chollosdelsuper'
                            ]"
                            v-bind:key="chollo.id"
                            :chollo_data="chollo"
                            :showed="() => actualIndex == 5"
                        />
                    </c-tab-panel>
                </c-tab-panels>
            </c-tabs>
        </c-box>
        <ChollosCreator
            :status="createChollosDrawer"
            :chollo_data="{ ...itemTemplate }"
            :is_saved="false"
        />
    </c-flex>
</template>

<script>
import { CBox, CFlex, CText, CButton, CIcon, CTabs } from "@chakra-ui/vue";
import ChollosItem from "../components/ChollosItem";
import { refreshToken } from "../common";
import ChollosCreator from "../components/ChollosCreator";

export default {
    CBox,
    CText,
    CFlex,
    CButton,
    CIcon,
    CTabs,

    components: {
        ChollosItem,
        ChollosCreator,
    },
    data() {
        return {
            items: { new: {}, popular: {}, featured: {} },
            drawerStatus: false,
            createChollosDrawer: false,
            refreshLoading: false,
            actualIndex: 0,
            currentOrder: "new",
        };
    },
    methods: {
        addNewItems(items) {
            if (!this.drawerStatus) {
                this.$delete(this.items, "new");
                this.$delete(this.items, "popular");
                this.$delete(this.items, "featured");
                this.$set(this.items, "new", items["new"] || {});
                this.$set(this.items, "popular", items["popular"] || {});
                this.$set(this.items, "featured", items["featured"] || {});
                this.refreshLoading = false;
            }
        },
        _sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms));
        },

        async refreshCommand() {
            this.refreshLoading = true;
            await this._sleep(2500);
            this.$socket.client.emit("update_items");
        },
        async _requestsForNewItems() {
            await this._sleep(1);
            setTimeout(this._requestsForNewItems, 180000);
            this.$socket.client.emit("update_items");
            refreshToken()
                .then(() => {})
                .catch(() => {});
        },
        handleItems(index) {
            this.actualIndex = index;
        },
    },
    activated() {
        this.$root.$emit("add_title", "Chollos");
    },
    mounted() {
        this._requestsForNewItems();

        this.$socket.$subscribe("add_new_items", (payload) => {
            this.addNewItems(payload);
        });
        this.$root.$on("_is_drawer_open", (status) => {
            this.drawerStatus = status;
        });
        this.$root.$on("_is_creator_open", (status) => {
            this.createChollosDrawer = status;
        });
    },
};
</script>

