.main__tabs-container {
    margin-top: 70px;
    padding: 30px 15px;
    min-height: calc(100vh - 123px);
    width: 100%;
}

.tabs__panel-container {
    border: 0;
    border-radius: 3px;
    color: #333;
    background: #fff;
    width: 100%;
    min-width: 680px;
    max-width: 1020px;
    height: 80%;
    max-height: 530px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
        0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
    overflow-y: auto;
}

.refresh__button {
    margin-left: 8px;
}
