<template>
    <c-stack marginBottom="24px">
        <c-form-label for="title">{{ name }}</c-form-label>

        <c-input
            variant="outline"
            :placeholder="placeholder"
            :show="show ? 'text' : 'password'"
            v-model="value"
            v-on:input="$emit('saveInputs', var_name, value)"
        />
    </c-stack>
</template>

<script>
export default {
    props: {
        placeholder: String,
        show: Boolean,
        name: String,
        var_name: String,
    },
    data() {
        return {
            value: "",
        };
    },
    methods: {
        setInputValues(values) {
            this.value = values[this.var_name];
            this.$root.$emit("onMountedParams", this.var_name, this.value);
        },
    },

    beforeMount() {
        this.$root.$emit("getConfigInputsValues", this.setInputValues);
        this.$root.$on("set_inputs_values", (values) => {
            this.setInputValues(values);
        });
    },
};
</script>

