<template>
  <div>
    <c-drawer
      :isOpen="this.$props.status.isOpen"
      placement="right"
      :on-close="close"
    >
      <c-drawer-overlay />
      <c-drawer-content>
        <c-drawer-close-button />

        <c-drawer-header> Configuracin de {{ name }} </c-drawer-header>

        <c-drawer-body>
          <c-form-control>
            <slot></slot>
          </c-form-control>
        </c-drawer-body>

        <c-drawer-footer>
          <c-button
            variant-color="blue"
            fontSize="14px"
            marginRight="8px"
            @click="saveSignal"
            >Guardar</c-button
          >

          <c-button @click="close()" fontSize="14px" marginRight="8px"
            >Cancelar</c-button
          >
        </c-drawer-footer>
      </c-drawer-content>
    </c-drawer>
  </div>
</template>

<script>
export default {
  props: {
    name: String,
    status: Object,
  },
  data() {
    return {};
  },
  methods: {
    close() {
      this.$props.status.isOpen = false;
    },
    saveSignal() {
      this.$emit("saveSignal", this.$props.name);
      this.close();
      this.showInfoSuccess("Configuracin guardada!")
    },
    showInfoSuccess(message) {
      this.$toast({
        title: "Info.",
        description: message,
        status: "info",
        duration: 9000,
        variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
      });
    },
  },
};
</script>