<template>
    <c-box mb="3" w="300px">
        <c-select
            v-model="botId"
            v-bind:input="callback(botId)"
            placeholder="Selecciona un Bot"
        >
            <option
                :key="bot.bot_id"
                v-for="bot in botData"
                :value="bot.bot_id"
            >
                {{ bot.name }}
            </option>
        </c-select>
    </c-box>
</template>
<script>
export default {
    props: {
        callback: {
            type: Function,
            required: true,
        },
    },
    data() {
        return {
            botId: "",
            botData: [],
        };
    },
    beforeMount() {
        this.botData = this.$store.state.bots;
        this.$nextTick(() => {
            try{
            this.botId = this.botData[0].bot_id
                ? this.botData[0].bot_id
                : this.$store.state.currentSelectedBotId;
            }catch(e){
                console.log(e);
            }
        });
    },
};
</script>