<template>
  <c-popover
    :trigger="value != true ? 'click' : 'hover'"
    v-slot="{ onClose }"
    placement="bottom"
  >
    <c-form-label html-for="email-alerts" marginLeft="4px" :fontSize="['0.75rem','0.9rem']">{{
      name[0].toUpperCase() + name.slice(1)
    }}</c-form-label>
    <c-popover-trigger>
      <c-switch
        :value="value"
        :isChecked="value"
        as="button"
        @click="handlePopOver(onClose)"
        v-on:input="handleValue"
      />
    </c-popover-trigger>
    <c-popover-content
      z-index="4"
      color="white"
      background-color="blue.700"
      border-color="blue.700"
    >
      <c-popover-header pt="4" font-weight="bold" border="0">
        Controlador de {{ name[0].toUpperCase() + name.slice(1) }}
      </c-popover-header>
      <c-popover-arrow />
      <c-popover-close-button />
      <c-popover-body>
        <!-- stack init-->

        <c-stack>
          <c-form-label for="eachNumber">Numero de repeticiones</c-form-label>

          <c-number-input
            id="eachNumber"
            :value="eachNumber"
            :max="100"
            clamp-value-on-blur
            :keep-within-range="false"
          >
            <c-number-input-field bg="blue.700" />
            <c-number-input-stepper>
              <c-number-increment-stepper />
              <c-number-decrement-stepper />
            </c-number-input-stepper>
          </c-number-input>

          <c-slider @change="handleChange" :default-value="0">
            <c-slider-track />
            <c-slider-filled-track />
            <c-slider-thumb />
          </c-slider>
        </c-stack>
        <!-- stack end-->

        <c-box mb="3" w="300px">
          <c-form-label for="selecTime">Repetir cada</c-form-label>
          <c-select
            v-model="timeToRepeat"
            id="selecTime"
            bg="blue.700"
            @change="saveConfig()"
          >
            <option value="0">No repetir</option>

            <option value="60">1m</option>
            <option value="300">5m</option>
            <option value="600">10m</option>
            <option value="900">15m</option>
            <option value="1200">20m</option>
            <option value="1800">30m</option>
            <option value="3600">1h</option>
            <option value="7200">2h</option>
            <option value="18000">5h</option>
          </c-select>
        </c-box>
      </c-popover-body>
    </c-popover-content>
  </c-popover>
</template>

<script>
export default {
  props: {
    name: String,
    func: Function,

    params: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      eachNumber: 1,
      timeToRepeat: "0",
      isOpen: false,
      value: true,
    };
  },

  methods: {
    handleChange(val) {
      this.eachNumber = val;
      this.saveConfig();
    },
    handlePopOver(func) {
      let value = !this.value;
      if (value != true) {
        func();
      }
      this.value = value;
    },

    handleValue() {
      this.saveConfig();
    },
    saveConfig() {
      let response = {
        name: this.name,
        content: {
          state: this.value,
          time: this.timeToRepeat,
          each_time: this.eachNumber,
        },
      };
      this.func(response);
    },

    handleSavedParams() {
      if (this.params && this.params.length >= 1) {
        this.params.forEach((element) => {
          if (this.name in element) {
            this.value = element[this.name].state;
            this.eachNumber = element[this.name].each_time;
            this.timeToRepeat = element[this.name].time;
            this.$forceUpdate();
          }
        });
        
      }
    },
  },

  mounted() {
    this.handleSavedParams();
    this.saveConfig();
  },
};
</script>

