<template>
  <c-stack marginBottom="24px">
    <c-form-label for="title">{{ name }}</c-form-label>
    <c-select
      v-model="value"
      v-on:change="$emit('saveInputs', var_name, value)"
    >
      <option value="chollito">Chollito</option>
      <option value="bitly">Bitly</option>
    </c-select>
  </c-stack>
</template>

<script>
export default {
  props: {
    name: String,
    returnFunc: Function,
  },
  data() {
    return {
      value: "chollito",
      var_name: "shortener",
    };
  },
  methods: {
    setInputValues(value) {
      this.value = value["shortener"];
      this.$root.$emit("onMountedParams", this.var_name, this.value);
    },
  },

  mounted() {
    this.$root.$emit("getConfigInputsValues", this.setInputValues);
  }
};
</script>

