<template>
    <div>
        <c-drawer
            :isOpen="status"
            placement="right"
            :on-close="close"
            :initialFocusRef="() => $refs.firstField"
            class="drawer-container-modal"
        >
            <c-drawer-overlay />
            <c-drawer-content class="drawer-content">
                <c-drawer-close-button />

                <c-drawer-header> Programar Chollo </c-drawer-header>

                <c-drawer-body id="drawer-body">
                    <c-form-control>
                        <c-stack spacing="24px">
                            <c-box>
                                <c-form-label for="title">Titulo</c-form-label>
                                <c-input
                                    ref="firstField"
                                    id="title"
                                    placeholder="Titulo del chollo"
                                    v-model="chollo.title"
                                />
                            </c-box>
                            <c-box>
                                <c-form-label for="market">Tienda</c-form-label>
                                <c-input
                                    id="market"
                                    placeholder="Tienda.. ej. Amazon"
                                    v-model="chollo.market"
                                />
                            </c-box>
                            <c-box>
                                <c-form-label for="original_price"
                                    >Precio original</c-form-label
                                >
                                <c-input-group>
                                    <c-input-left-element
                                        color="gray.300"
                                        fontSize="1.2em"
                                        ></c-input-left-element
                                    >
                                    <c-input
                                        id="original_price"
                                        placeholder="Precio original.."
                                        v-model="chollo.regular_price"
                                    />
                                </c-input-group>

                                <c-form-label for="current_price"
                                    >Precio oferta</c-form-label
                                >
                                <c-input-group>
                                    <c-input-left-element
                                        color="gray.300"
                                        fontSize="1.2em"
                                        ></c-input-left-element
                                    >
                                    <c-input
                                        id="current_price"
                                        v-model="chollo.price"
                                        placeholder="Precio oferta.."
                                    />
                                </c-input-group>

                                <c-form-label for="discount_code"
                                    >Cdigo de descuento</c-form-label
                                >
                                <c-input-group>
                                    <c-input
                                        id="discount_code"
                                        v-model="chollo.discount"
                                        placeholder="Cdigo de descuento.."
                                    />
                                </c-input-group>
                            </c-box>
                            <c-box>
                                <c-form-label for="url">Url</c-form-label>
                                <c-input-group>
                                    <c-input
                                        type="url"
                                        id="url"
                                        placeholder="Url del chollo"
                                        rounded="0"
                                        v-model="chollo.chollo_url"
                                    />
                                </c-input-group>
                            </c-box>

                            <c-box>
                                <c-form-label for="time"
                                    >Publicar en</c-form-label
                                >
                                <c-input-group>
                                    <c-input
                                        type="time"
                                        v-model="chollo.time"
                                        id="time"
                                    ></c-input>
                                </c-input-group>
                            </c-box>

                            <c-box>
                                <SwitchButton
                                    :value="false"
                                    :name="response.telegram.name"
                                    :func="saveSwitchFunc"
                                    :params="chollo.send_to"
                                />
                                <SwitchButton
                                    :value="false"
                                    :name="response.twitter.name"
                                    :func="saveSwitchFunc"
                                    :params="chollo.send_to"
                                />
                                <SwitchButton
                                    :value="false"
                                    :name="response.facebook.name"
                                    :func="saveSwitchFunc"
                                    :params="chollo.send_to"
                                />
                            </c-box>
                            <c-box
                                class="uploadFileButtons__container--wrappers"
                                display="flex"
                            >
                                <input
                                    type="file"
                                    ref="file"
                                    style="display: none"
                                    accept="image/*"
                                    v-on:change="uploadImage($event)"
                                />
                                <c-button
                                    @click="$refs.file.click()"
                                    variantColor="yellow"
                                    class="
                                        container__uploadFileButtons--by-attach
                                    "
                                    ><c-icon name="file-image" />Subir
                                    Imagen</c-button
                                >
                                <LoadFromUrl :callback="updateImageUrl" />
                            </c-box>
                            <c-box class="uploadFileImage__container--wrapper">
                                <c-image
                                    size="100px"
                                    objectFit="cover"
                                    :src="chollo.image_url"
                                />
                            </c-box>
                            <c-box>
                                <c-form-label for="desc"
                                    >Descripcion</c-form-label
                                >
                                <c-textarea
                                    id="desc"
                                    :value="chollo.description"
                                    v-model="chollo.description"
                                />
                            </c-box>
                            <c-flex w="100%">
                                <ImageEditor
                                    :eventHandler="updateImageUrl"
                                    :chollo_data="chollo"
                                />
                            </c-flex>
                            <c-box v-if="!is_saved">
                                <BotSelector :callback="addBotToItem" />
                            </c-box>
                        </c-stack>
                    </c-form-control>
                </c-drawer-body>

                <c-drawer-footer>
                    <ConfirmationButton
                        title="Publicar Chollo"
                        name="Publicar Ahora"
                        message="Est seguro que desea publicar este chollo?"
                        :type="{ action: { color: 'green' }, color: 'green' }"
                        :action="publishItem"
                        :focusButton="true"
                    />

                    <ConfirmationButton
                        :title="
                            !is_saved ? 'Programar Chollo' : 'Editar Chollo'
                        "
                        name="Programar"
                        :message="
                            !is_saved
                                ? 'Est seguro que desea programar este chollo?'
                                : 'Est seguro que desea editar este chollo?'
                        "
                        :type="{ action: { color: 'blue' }, color: 'blue' }"
                        :action="!is_saved ? saveItem : editItem"
                        :focusButton="true"
                    />

                    <ConfirmationButton
                        v-if="is_saved"
                        title="Eliminar Chollo"
                        name="Eliminar"
                        message="Est seguro que desea eliminar este chollo?"
                        :type="{ action: { color: 'red' }, color: 'red' }"
                        :action="deleteItem"
                    />
                </c-drawer-footer>
            </c-drawer-content>
        </c-drawer>
    </div>
</template>

<script>
import SwitchButton from "./SwitchButton";
import ConfirmationButton from "./alerts/ConfirmationButton";
import ImageEditor from "./image/ImageEditor";
import LoadFromUrl from "./image/components/LoadFromUrl.vue";
import BotSelector from "./BotSelector";
import {getImageUrl, getUuid} from "../common";
import {getUiid} from "../common";

export default {
    getUiid,

    components: {
        SwitchButton,
        ConfirmationButton,
        ImageEditor,
        BotSelector,
        LoadFromUrl,
    },

    props: {
        status: Boolean,
        chollo_data: {
            type: Object,
            required: true,
        },

        is_saved: {
            type: Boolean,
            required: false,
            default: false,
        },
    },
    data() {
        return {
            response: {
                telegram: this._new_params("telegram"),
                twitter: this._new_params("twitter"),
                facebook: this._new_params("facebook"),
            },
            chollo: this._new_chollo(),
        };
    },

    methods: {
        close() {
            this.$props.status = false;
            this.$root.$emit("_is_creator_open", this.status);
        },
        saveSwitchFunc(data) {
            let content = {};
            let name = data.name;

            content[name] = data.content;
            this._fixSize(name);
            this.chollo.send_to.push(content);
            this.$root.$emit("requestItemChange", this.chollo);
        },
        _new_params(value) {
            return { name: value, time: "", each_number: 0, state: false };
        },
        addBotToItem(botId) {
            this.$store.state.currentSelectedBotId = botId;
            this.chollo.bot_id = botId;
        },
        saveItem() {
            if (this.chollo.time && this.chollo.bot_id) {
                this.$root.$emit("save_item", this.chollo);
                this.showInfoSuccess(
                    "El chollo ha sido programado exitosamente!"
                );
                this.chollo = this._new_chollo();
                this.close();
            } else {
                this.showInfoError(
                    "Debe seleccionar una hora y bot para poder programar este chollo!"
                );
            }
        },
        editItem() {
            this.$root.$emit("edit_item", this.chollo);
            this.showInfoSuccess("El chollo ha sido editado exitosamente!");
            this.close();
        },
        deleteItem() {
            this.$root.$emit("delete_item", this.chollo);
            this.showInfoSuccess("El chollo ha sido eliminado exitosamente!");
            this.close();
        },
        publishItem() {
            this.$root.$emit("publish_item", this.chollo);
            this.chollo = this._new_chollo();
            this.close();
        },
        showInfoSuccess(message) {
            this.$toast({
                title: "Info.",
                description: message,
                status: "info",
                duration: 9000,
                variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
            });
        },
        showInfoError(message) {
            this.$toast({
                title: "Error.",
                description: message,
                status: "error",
                duration: 9000,
                variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
            });
        },
        _fixSize(name) {
            try {
                this.chollo.send_to.forEach((element, i) => {
                    if (name in element) {
                        this.chollo.send_to.splice(i, 1);
                    }
                });
            } catch {
                this.chollo.send_to = [];
            }
        },
        updateImageUrl(url) {
            this.chollo.custom_image = url;
            this.chollo.image_url = url;
            this.chollo.original_image = url;
        },

        handleLinked() {
            this.chollo.is_linked = !this.chollo.is_linked;
            if (this.chollo.is_linked) {
                this.chollo.chollo_url = this.chollo.linked_url;
            } else {
                this.chollo.chollo_url = this.chollo.original_chollo_url;
            }
        },

        uploadImage(e) {
            let file = e.target.files[0];
            getImageUrl(file, this.updateImageUrl);
        },

        _new_chollo() {
            return {title: '', description: '', price: '', regular_price: '', is_linked: false, is_soydechollos: true, original_chollo_url: '', linked_url: '', market: '', id: getUuid(), chollo_url: '', index: 0, image_url: 'https://i.ibb.co/gzn1TgC/No-image-found-1.jpg', original_url: 'https://i.ibb.co/gzn1TgC/No-image-found-1.jpg', discount: ''}
        }
    }
};
</script>

<style >
.drawer-container-modal {
    z-index: 1500;
}

#drawer-body {
    overflow-x: auto;
}

@media (max-height: 850px) {
    .drawer-content > section {
        height: 86vh !important;
    }
}
</style>