import Vue from 'vue'
import Chakra, { CThemeProvider } from '@chakra-ui/vue'
import App from './App.vue'
import { faTag, faTags, faSignOutAlt, faCog, faShoppingCart, faFilter,faPlus, faRobot, faInfoCircle, faLink, faBars, faHistory, faExternalLinkAlt, faCircle, faSync, faFileImage} from '@fortawesome/free-solid-svg-icons';
import VueSocketIOExt from 'vue-socket.io-extended';
import { io } from 'socket.io-client';
import router from './router'
import store from './store'

const namespace = "/app"
const port = location.port;

const socket = io(location.protocol + '//' + document.domain + ':' + port + namespace);

Vue.use(VueSocketIOExt, socket);
Vue.use(Chakra, {
  icons: {
    // Here we state that we use `fa`
    // icons library for Chakra's
    // internal icon parser
    iconPack: 'fa',
    iconSet: {
      faTag,
      faPlus,
      faTags,
      faSignOutAlt,
      faCog,
      faShoppingCart,
      faLink,
      faBars,
      faHistory,
      faExternalLinkAlt,
      faCircle,
      faSync,
      faRobot,
      faInfoCircle,
      faFilter,
      faFileImage
    }
  }
})

new Vue({
  el: '#app',
  router,
  store,
  render: (h) => h(CThemeProvider, [h(App)])
}).$mount()