<template>
  <c-box h="100%" w="100%" v-if="render">
    <vue-p5
      :key="this.generateId()"
      @setup="setup"
      @draw="draw"
      @preload="preload"
    >
    </vue-p5>
  </c-box>
</template>

<script>
import RobotoBlack from "../fonts/Roboto-Black.ttf";
import RobotoLigth from "../fonts/Roboto-Medium.ttf";
import ImageBuilder from "./ImageRender.mjs";
import VueP5 from "vue-p5";

export default {
  components: {
    "vue-p5": VueP5,
  },
  props: {
    image_data: {
      type: Object,
      require: true,
    },

    func: Function,
  },

  data() {
    return {
      channel_template_image: null,
      background_template_image: null,
      coupon_template_image: null,
      sketch: null,
      imgbb_token: "77250bacf66046bd48da9c0996f21ced",
      title: "",
      pvp: "",
      price: "",
      market: "",

      render: true,
      builder_data: {},
    };
  },
  methods: {
    preload(sketch) {
      this.builder_data = {
        title: this.title,
        price: this.price,
        market: this.market,
        regularPrice: this.pvp,
        imageData: this.image_data,
        fonts: { robotoBlack: null, robotoLigth: null },
      };

      sketch.loadFont(RobotoBlack, (font) => {
        this.builder_data.fonts.robotoBlack = font;
      });
      sketch.loadFont(RobotoLigth, (font) => {
        this.builder_data.fonts.robotoLigth = font;
      });
    },
    setup(sketch) {
      sketch.createCanvas(1000, 1000);
      sketch.frameRate(10);
      this.sketch = sketch;

      this.imageBuilder = new ImageBuilder(sketch, this.builder_data);
      this.$emit("sketchReady");
    },
    draw(sketch) {
      sketch.clear();
      try {
        this.imageBuilder.render();
      } catch (err) {
        console.log(err);
      }
    },
    generateId() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, (c) => {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    },
    saveImage() {
      let currentFrame = () => {
        const ctx = document.getElementById("defaultCanvas0").getContext("2d");
        return ctx.canvas.toDataURL("image/png").split(";base64,")[1];
      };
      this.getImageUrl(currentFrame());
    },

    getImageUrl(frame) {
      let form = new FormData();
      form.append("image", frame);
      fetch("https://api.imgbb.com/1/upload?&key=" + this.imgbb_token, {
        method: "POST",
        body: form,
      })
        .then(async (response) => await response.json())
        .then((data) => {
          this.$root.$emit("updateBanner", data.data.url);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    showInfoSuccess(message) {
      this.$toast({
        title: "Info.",
        description: message,
        status: "info",
        duration: 9000,
        variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
      });
    },
  },

  mounted() {
    this.$on("sketchReady", () => {
      this.$root.$on("updateTextTitle", (text) => {
        this.imageBuilder.onUpdateTitle(text);
      });

      this.$root.$on("updateTextPrice", (text) => {
        this.imageBuilder.onUpdatePrice(text);
      });

      this.$root.$on("updateTextRegularPrice", (text) => {
        this.imageBuilder.onUpdateRegularPrice(text);
      });

      this.$root.$on("addMarket", (text)=>{
        this.imageBuilder.onUpdateMarket(text);
      })

      this.$root.$emit("getImageFormData");
    });

    this.$root.$once("destroyP5Instance", () => {
      this.sketch.remove();
      this.sketch.p5 = null;
      this.render = false;
      this.$root.$delete(this.imageBuilder);
      this.$root.$off("updateTextRegularPrice");
      this.$root.$off("updateTextPrice");
      this.$root.$off("addMarket");
      this.$root.$off("updateTextTitle");
      this.$root.$off("saveImage");
    });

    this.$root.$once("saveImage", (callback) => {
      callback(this.saveImage);
    });
  },
  beforeDestroy() {
    this.$root.$off("destroyP5Instance");
  },
};
</script>

<style>
#defaultCanvas0 {
  height: 500px !important;
  width: 500px !important;
}

@media (max-width: 1500px) {
  #defaultCanvas0 {
    width: 100% !important;
    height: 100% !important;
  }
}
</style>