<template>
    <c-flex align="center" justify="start" id="sidebar__menu">
        <c-box
            h="100%"
            bg="white"
            class="sidebar__menu-sidebar"
            v-bind:class="validateMobileView()"
        >
            <c-box bg="white" w="100%" class="sidebar__menu-logo">
                <c-flex align="center" justify="center" w="100%">
                    <c-box class="sidebar__close-button"
                        ><c-close-button size="md" @click="openMobileSidebar()"
                    /></c-box>
                    <c-text color="black" fontSize="24px">{{ title }}</c-text>
                </c-flex>
            </c-box>
            <c-box class="sidebar__menu-button-container">
                <c-flex justify="center" align="center" direction="column">
                    <c-box
                        class="sidebar__menu-button"
                        as="button"
                        @click="
                            $router.push('/chollos/').catch((err) => {});
                            switchTo(0);
                        "
                        :class="validateView(0)"
                    >
                        <c-text id="sidebar__menu-buttons-text"
                            ><c-icon name="tag" /> Chollos </c-text
                        ></c-box
                    >
                    <c-box
                        class="sidebar__menu-button"
                        as="button"
                        @click="
                            $router
                                .push('/chollos/programados')
                                .catch((err) => {});
                            switchTo(1);
                        "
                        :class="validateView(1)"
                        ><c-text id="sidebar__menu-buttons-text"
                            ><c-icon name="tags" /> Chollos programados</c-text
                        ></c-box
                    >
                    <c-box
                        class="sidebar__menu-button"
                        as="button"
                        @click="
                            $router.push('/bots').catch((err) => {});
                            switchTo(2);
                        "
                        :class="validateView(2)"
                        ><c-text id="sidebar__menu-buttons-text"
                            ><c-icon name="robot" /> Bots</c-text
                        ></c-box
                    >
                    <c-box
                        class="sidebar__menu-button"
                        as="button"
                        @click="logout()"
                        :class="validateView(3)"
                    >
                        <c-text id="sidebar__menu-buttons-text"
                            ><c-icon name="sign-out-alt" /> Salir</c-text
                        ></c-box
                    >
                </c-flex>
                <c-stack :spacing="4" is-inline class="sidebar__menu-version-container">
                    <c-tag class="version-text" :size="['sm']" variant-color="red">
                        <c-tag-label> Scr44gr@v{{ app_version }} </c-tag-label>
                    </c-tag>
                </c-stack>
            </c-box>
        </c-box>
    </c-flex>
</template>

<script>
import { CBox, CFlex, CText, CButton, CIcon } from "@chakra-ui/vue";

export default {
    CBox,
    CText,
    CFlex,
    CButton,
    CIcon,

    props: {
        title: String,
        app_version: String,
    },

    data: function () {
        return {
            view: null,
            isOpen: false,
            close: true,
        };
    },
    created: function () {
        let view = {
            "/chollos/": 0,
            "/chollos/programados": 1,
            "/bots": 2,
        };
        this.switchTo(view[this.$router.history.current.path]);
    },
    methods: {
        switchTo(view) {
            this.openMobileSidebar();
            this.view = view;
            this.$emit("toView", view);
        },

        validateView(view) {
            if (this.view == view) {
                return "sidebar__menu-button--selected";
            } else {
                return "";
            }
        },

        validateMobileView() {
            let width =
                window.innerWidth ||
                document.documentElement.clientWidth ||
                document.body.clientWidth;
            if (this.isOpen == true && width <= 600) {
                return "mobile__sidebar--open";
            } else if (width >= 600) {
                return "";
            }
            return "";
        },

        openMobileSidebar() {
            this.isOpen = !this.isOpen;
            this.close = !this.close;
        },
        logout() {
            window.open("/logout", "_self");
        },
    },

    mounted() {
        this.$root.$on("openSidebar", this.openMobileSidebar);
    },
};
</script>

<style>
#sidebar__menu {
    position: relative;
    top: 0;
    height: 100vh;
    float: left;
    font-weight: 300;
}

.sidebar__menu-sidebar {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 0;
    width: 260px;
    box-shadow: 0 16px 38px -12px rgba(0, 0, 0, 0.56),
        0 4px 25px 0 rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
    overflow-y: hidden;
    max-height: 100%;
}

.sidebar__menu-logo {
    width: 100%;
    height: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebar__menu-button-container {
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}

.sidebar__close-button {
    display: none;
    position: absolute;
    right: 0;
    left: 1;
    margin-right: 16px;
}

.sidebar__menu-button {
    background-color: white;
    width: 90%;
    height: 50px;
    padding: 14px;
    margin: 0 0 16px;
    border-radius: 3px;
    outline: none;
    text-align: start;
    transition: all 150ms ease-in;
}

.sidebar__menu-button-text {
    color: black;
    text-align: start;
}

.sidebar__menu-version-container {
    display: flex;
    width: 100%;
    height: calc(100% - 280px);
    justify-content: center;
    align-items: flex-end;
    padding-left: 4px;
}

.sidebar__menu-button--selected {
    background-color: #ff0055;
    color: whitesmoke;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.14),
        0 7px 10px -5px rgba(244, 67, 54, 0.4);
}
.sidebar__menu-button--selected:hover {
    background-color: #ff0055 !important;
}

.sidebar__menu-button:hover {
    background-color: #dbe0e6;
}
/* for mobile */
@media only screen and (max-width: 900px),
    not screen and (min-device-width: 600px) {
    .sidebar__menu-sidebar {
        width: 90px;
        overflow-x: hidden;
    }
    #sidebar__menu-buttons-text {
        visibility: hidden;
        text-align: center !important;
        font-size: 0;
    }
    #sidebar__menu-buttons-text > svg {
        visibility: initial;
        font-size: initial;
    }
}

@media only screen and (max-width: 600px) {
    .sidebar__menu-sidebar {
        display: none;
        width: 100%;
        z-index: 1;
    }

    .mobile__sidebar--open {
        display: block;
        position: fixed;
        top: 0;
        right: 0;
        left: auto;
    }
    #sidebar__menu-buttons-text {
        visibility: initial;
        text-align: center;
        font-size: initial;
    }
    .sidebar__close-button {
        display: initial;
    }
}
</style>