<template>
    <div>
        <c-button
            variantColor="blue"
            class="container__uploadFileButtons--by-url"
            ml="4px"
            @click="isOpen = true"
            ><c-icon name="link" />Aadir via URL
        </c-button>
        <c-modal :is-open="isOpen" :on-close="close">
            <c-modal-content ref="content">
                <c-modal-header>Cargar la imagen por URL</c-modal-header>
                <c-modal-close-button />
                <c-modal-body>
                    <c-form-control>
                        <c-form-label> Url de la imagen </c-form-label>
                        <c-input
                            ref="initialRef"
                            placeholder="Ej. https://soydechollos.com/image.jpg"
                            v-model="url"
                        />
                    </c-form-control>
                </c-modal-body>

                <c-modal-footer>
                    <c-button
                        variant-color="blue"
                        mr="3"
                        @click="()=>{callback(url); close()}"
                    >
                        Establecer</c-button
                    >
                    <c-button @click="close">Cancelar</c-button>
                </c-modal-footer>
            </c-modal-content>
            <c-modal-overlay />
        </c-modal>
    </div>
</template>

<script>
export default {

    props: {
        callback: {
            type: Function,
            required: true
        },
    },
    data() {
        return { isOpen: false, url:""};
    },
    methods: {
        open() {
            this.isOpen = true;
        },
        close() {
            this.isOpen = false;
        },
    },
};
</script>