<template>
    <transition name="fade" mode="out-in">
        <c-flex
            justify="start"
            direction="column"
            class="item__container"
            v-if="showed()"
        >
            <c-box
                as="button"
                p="8px"
                class="item__box-container"
                w="auto"
                margin="16px"
                borderWidth="1px"
                display="flex"
            >
                <c-box @click="openUrl(chollo.linked_url)">
                    <c-image
                        :src="chollo.image_url"
                        class="item__image"
                        size="32"
                        marginRight="8px"
                    />
                </c-box>

                <c-stack
                    :spacing="2"
                    direction="row"
                    justify="space-between"
                    w="100%"
                    class="item__stack-container"
                >
                    <c-stack
                        :spacing="2"
                        direction="column"
                        justify="space-between"
                        textAlign="start"
                        w="100%"
                    >
                        <c-box
                            display="flex"
                            direction="row"
                            w="100%"
                            justifyContent="space-between"
                        >
                            <c-text
                                :fontSize="['sm', 'md', 'lg', 'xl']"
                                fontWeight="700"
                                >{{ chollo.title }}</c-text
                            >
                            <c-box>
                                <c-badge
                                    mx="2"
                                    bg="rgb(228, 121, 17)"
                                    color="white"
                                    fontWeight="400"
                                    p="4px"
                                    :fontSize="['8px', '14px']"
                                    ><c-icon
                                        size="14px"
                                        marginBottom="3px"
                                        name="shopping-cart"
                                    />
                                    {{ chollo.market }}</c-badge
                                >
                            </c-box>
                        </c-box>
                        <c-text
                            color="rgba(0,0,0,.6)"
                            h="50px"
                            fontSize="0.8rem"
                            maxHeight="120px"
                            >{{ chollo.description.substring(0, 120) }}</c-text
                        >

                        <c-box
                            w="100%"
                            :spacing="2"
                            display="flex"
                            justifyContent="space-between"
                            class="item__buttons-container"
                        >
                            <c-box w="auto" class="item__price-info">
                                <c-tag
                                    variant-color="red"
                                    as="s"
                                    :fontSize="['0.6rem', 'md', 'lg', 'xl']"
                                    v-if="chollo.regular_price != undefined"
                                    marginRight="8px"
                                    >{{ chollo.regular_price }}</c-tag
                                >
                                <c-tag
                                    variant-color="green"
                                    :fontSize="['0.6rem', 'md', 'lg', 'xl']"
                                    v-if="chollo.price != undefined"
                                    >{{ chollo.price }}</c-tag
                                >
                            </c-box>
                            <c-box flexDirection="row">
                                <c-tag
                                    v-if="is_saved"
                                    id="scheludedTime"
                                    mr="8px"
                                    variant-color="red"
                                    >{{ chollo.time }}</c-tag
                                >

                                <c-button
                                    marginRight="4px"
                                    bg="#0038aa"
                                    :fontSize="['0.6rem', 'md', 'lg']"
                                    :m="['lg', 'md']"
                                    color="white"
                                    :_hover="{
                                        bg: 'white',
                                        color: '#0038aa',
                                        border: '1px solid #0038aa',
                                    }"
                                    @click="openUrl(chollo.chollo_url)"
                                    >Ir al chollo</c-button
                                >
                                <c-button
                                    @click="openDrawer()"
                                    :fontSize="['0.6rem', 'md', 'lg']"
                                    >{{
                                        !is_saved ? "Programar" : "Editar"
                                    }}</c-button
                                >
                            </c-box>
                        </c-box>
                    </c-stack>
                </c-stack>
            </c-box>
            <ChollosDrawer
                v-if="status.isOpen"
                :status="status"
                :chollo_data="chollo"
                :is_saved="is_saved"
            />
        </c-flex>
    </transition>
</template>

<script>
import {
    CBox,
    CFlex,
    CText,
    CStack,
    CTag,
    CButton,
    CBadge,
} from "@chakra-ui/vue";

import ChollosDrawer from "./ChollosDrawer";

export default {
    CBox,
    CText,
    CFlex,
    CTag,
    CStack,
    CButton,
    CBadge,

    components: {
        ChollosDrawer,
    },

    props: {
        chollo_data: Object,
        showed: Function,
        is_saved: {
            type: Boolean,
            required: false,
            default: false,
        },
    },

    data() {
        return {
            status: { isOpen: null, chollo: {} },
        };
    },
    methods: {
        openDrawer() {
            this.status.isOpen = true;
            this.$root.$emit("_is_drawer_open", this.status.isOpen);
        },
        openUrl(url) {
            window.open(url);
        },
        handleChanges(item) {
            if (this.chollo.id == item.id) {
                this.chollo = item;
            }
        },
    },

    beforeMount() {
        this.chollo = { ...this.chollo_data };
        if (!this.chollo.send_to) {
            this.chollo.send_to = [];
        }
    },

    created() {
        this.$root.$on("requestItemChange", this.handleChanges);
    },

    beforeDestroy() {
        this.$root.$off("requestItemChange");
    },
};
</script>

<style>
.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.4s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
}

@media (max-width: 700px) {
    #scheludedTime {
        display: none;
    }
}
</style>