import Vue from 'vue'
import VueRouter from 'vue-router'
import New from '../views/New.vue'
import Stored from '../views/Stored'
import Config from '../views/Config'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    components: {
      default: New
    }
  },
  {
    path: '/chollos/',
    name: 'chollos',
    component: New
  },
  {
    path: '/chollos/programados',
    name: 'programados',
    component: Stored
  },
  {
    path: '/bots/',
    name: 'bots',
    component: () => import(/* webpackChunkName: "bots" */ '../views/Bots.vue'),
  },
  {
    path: '/bots/configuracion/:id',
    name: 'bot_config',
    component: Config
  },
  {
    path: '/bots/*',
    redirect: '/chollos/'
  },
  {
    path: '/chollos/*',
    redirect: '/chollos/'
  },
]

const router = new VueRouter({
  routes
})

export default router
