<template>
  <c-flex flexDirection="row" class="logs__flex-container" width="100%" justifyContent="space-between">
    <c-box fontSize="0.85rem" color="white">{{ name.substring(0, 8) }}</c-box>
    <c-link :href="url" is-external fontSize="0.8rem" color="#ff0055">
      {{ url.replace("https://", "") }} <c-icon name="external-link-alt" mx="0.9rem" />
    </c-link>
    <c-box fontSize="0.8rem" color="white">{{ this.getLocalTime() }}</c-box>
  </c-flex>
</template>

<script>
export default {
  props: {
    name: String,
    url: String,
  },
  methods: {
    getLocalTime() {
      let d = new Date();
      let pm = d.getHours() >= 12;
      let hour12 = d.getHours() % 12;
      if (!hour12) hour12 += 12;
      let minute = d.getMinutes();
      if (minute <= 9) {
        minute = `0${minute}`;
      }
      return `${hour12}:${minute} ${pm ? "pm" : "am"}`;
    },
  },
};
</script>

